"use client";

import { Box, Heading, Text, Stack, Field, Input, Button, Separator,SimpleGrid, Flex, Switch } from "@chakra-ui/react";
import DashboardLayout from "../components/layout/DashboardLayout";

export default function SettingsPage() {
  return (
    <DashboardLayout>
      <Box maxW="3xl">
        <Heading size="lg" mb={2}>Settings</Heading>
        <Text color="gray.500" mb={8}>Manage your account preferences and application settings.</Text>

        <Stack gap={10}>
          {/* Profile Section */}
          <Box>
            <Heading size="md" mb={4}>Profile Information</Heading>
            <Stack gap={4} p={6} bg="white" _dark={{ bg: "gray.900" }} borderRadius="2xl" boxShadow="sm">
              <SimpleGrid columns={{ base: 1, md: 2 }} gap={4}>
                <Field.Root>
                  <Field.Label>Display Name</Field.Label>
                  <Input placeholder="Your Name" defaultValue="Admin User" />
                </Field.Root>
                <Field.Root>
                  <Field.Label>Email Address</Field.Label>
                  <Input placeholder="email@example.com" />
                </Field.Root>
              </SimpleGrid>
              <Button colorPalette="teal" alignSelf="flex-start" size="sm">Save Changes</Button>
            </Stack>
          </Box>

          <Separator />

          {/* Preferences Section */}
          <Box>
            <Heading size="md" mb={4}>Preferences</Heading>
            <Stack gap={4} p={6} bg="white" _dark={{ bg: "gray.900" }} borderRadius="2xl" boxShadow="sm">
              <Flex justify="space-between" align="center">
                <Box>
                  <Text fontWeight="bold">Email Notifications</Text>
                  <Text fontSize="sm" color="gray.500">Receive weekly summary reports via email.</Text>
                </Box>
                <Switch.Root colorPalette="teal" defaultChecked>
                  <Switch.Control>
                    <Switch.Thumb />
                  </Switch.Control>
                </Switch.Root>
              </Flex>
            </Stack>
          </Box>
        </Stack>
      </Box>
    </DashboardLayout>
  );
}